import { Injectable, Inject } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';

import {Subject} from 'rxjs/Subject';
import {Observable} from 'rxjs/Observable';
import {Observer} from 'rxjs/Observer';
import 'rxjs/add/observable/from';
import 'rxjs/add/observable/merge';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/observable/fromPromise'
import 'rxjs/add/operator/mergeMap';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/mapTo';
import 'rxjs/add/operator/switchMap';
import 'rxjs/add/operator/switchMapTo';
import 'rxjs/add/operator/takeWhile';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';

@Injectable()
export class HttpConnectionService {

  public isAuth: boolean = false;
  public authStream = new Subject();

  constructor(private http:Http) {

  		this.getProfileInfo().subscribe(user=>{
  			this.isAuth = true;
  	},
  	()=>{
  			this.isAuth = false;

  	});


   }


  public singin(formData){
  		return	Observable.create((observer:Observer<any>)=>{
  	 		let body = JSON.stringify(formData);
		    let headers = new Headers({ 'Content-Type': 'application/json' });
		    let options = new RequestOptions({ headers: headers, withCredentials: false});
		  	this.http.post(`http://localhost:3000/api/singin`,body,options)
		  	.subscribe((responce)=>{
		  		let isSucces = JSON.parse(responce["_body"]).isSucces;
		  		console.log(responce)

		         if(isSucces){
		          	observer.next(JSON.parse(responce["_body"]));
		        }
		         if(!isSucces){
		         	observer.error(JSON.parse(responce["_body"]));
		       }

		    });

  	 })
   }



   public login(formData){
	  		return	Observable.create((observer:Observer<any>)=>{
	  	 		let body = JSON.stringify({username:formData.login, password: formData.password});

			    let headers = new Headers({ 'Content-Type': 'application/json' });
			    let options = new RequestOptions({ headers: headers, withCredentials: true});
			  	this.http.post(`http://localhost:3000/api/login`,body,options)
			  	.subscribe((responce)=>{
			  		let isSucces = JSON.parse(responce["_body"]).isSucces;
			  		console.log(responce)

			         if(isSucces){
			         	this.authStream.next(JSON.parse(responce["_body"]).user);
			          	observer.next(JSON.parse(responce["_body"]));
			        }
			         if(!isSucces){
			         	observer.error(JSON.parse(responce["_body"]));
			       }

			    });

	  	 })

   }



   public getProfileInfo(){
   		return	Observable.create((observer:Observer<any>)=>{


			    let headers = new Headers({ 'Content-Type': 'application/json' });
			    let options = new RequestOptions({ headers: headers, withCredentials: true});
			  	this.http.get(`http://localhost:3000/api/getProfile`,options)
			  	.subscribe((responce)=>{
			  		let isSucces = JSON.parse(responce["_body"]).isSucces;
			  		console.log(responce)

			         if(isSucces){
			         	this.authStream.next(JSON.parse(responce["_body"]).user);
			          	observer.next(JSON.parse(responce["_body"]));
			        }
			         if(!isSucces){
			         	observer.error(JSON.parse(responce["_body"]));
			       }

			    });

	  	 })

   }


   public logout(){

   	
   }







}



